.chat{
    flex:0.75;
    display: flex;
    flex-direction: column;
}
.chat__header{
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}
.chat__headerInfo{
    flex: 1;
    padding-left: 20px;
}
.chat__headerInfo >h3{
    margin-bottom: 3px;
    font-weight: 500;
    
}
.chat__headerInfo > p{
    color: gray;
}
.chat__headerRight{
    display: flex;
    justify-content: space-between;
    /* here */
    min-width: 100px;
}
.chat__body{
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    flex: 1;
    background-repeat: repeat;
    background-position: center;
    padding:30px;
    overflow: scroll;
}

/* Hide the scrollbar for chrome , safari,Opera */
.chat__body::-webkit-scrollbar{
    display:none ;
}

/* HIde the scrollbar for IE,Edge,Firefox */
.chat__body{
    -ms-overflow-style: none; /*IE and Edge*/
    scrollbar-width: none; /*Firefox */
}
.chat__message{
    position: relative;
    font-size: 16px;
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    word-break: break-all; 
    background-color: #ffffff;
    margin-bottom: 30px;
}
.chat__name{
    top: 0px;
    font-weight: 800;
    font-size: xx-small;
    position: absolute;
    top: -15px;
    
}
.chat__timestamp{
    margin-left: 10px;
    font-size: xx-small;
}
.chat__receiver{
    margin-left: auto;
    background-color: #dcf8ce;
}

.chat__footer > form{
    flex: 1;
    display: flex;
    
}
.chat__footer > form > input {
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    
}



.chat__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
    
}
.chat__footer > .MuiSvgIcon-root{
    padding: 10px;
    color: gray;
}
