.sidebar{
    display: flex;
    flex-direction: column;
    
    flex: 0.35;
}

.sidebar__header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right:1px solid lightgray;
}
.sidebar__headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar__headerRight > .MuiSvgIcon-root{
    font-size: 20px !important;
    margin-right: 2vw;
}
.sidebar__search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
    }
.sidebar__searchContainer{
    display: flex;
    align-items: center;
    background-color: #fff;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}
.sidebar__searchContainer > input{
    border: none;
    margin-left: 10px;
}
.sidebar__searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}
a{
    text-decoration: none !important;
    color: #000000;
}
.sidebar__chats{
    /* display: flex; */
    flex: 1;
     background-color: #fff;
     overflow: scroll;
}
/* Hide the scrollbar for chrome , safari,Opera */
.sidebar__chats::-webkit-scrollbar{
    display:none ;
}

/* HIde the scrollbar for IE,Edge,Firefox */
.sidebar__chats{
    -ms-overflow-style: none; /*IE and Edge*/
    scrollbar-width: none; /*Firefox */
}
